export default function DrumMachine(props) {
    //set state to switch sound banks
    const [bank, setBank] = useState(bankOne);

    return (
        <div id="drum-machine">
            <DrumPad bank={bank} setBank={setBank} setSoundText={props.setSoundText} playAudio={props.playAudio}/>
            <div id="controls">
                <div id="display">
                    {props.soundText}
                </div>
                <div id="switch">
                    <BankSwitch bank={bank} setBank={setBank}/>
                </div>
            </div>
        </div>
    )
}


